import {api} from '/js/api'
import {Languages, request} from "../../js/util";

api({});

const lng = Languages.package;
const from=request['from'];
let clng = 'en';

if (localStorage.getItem('lngPkg') != null || localStorage.getItem('lngPkg') != undefined) {
    const currentlng = localStorage.getItem('lngPkg');
    clng = currentlng.replace(/["""]/g, '');


}

$('#sec').html(template('tpl', {request, lng: Languages.package}));

document.getElementById('iptSearch').addEventListener('keypress', function (evt) {
    if (evt.keyCode === 13 && this.value) {
        //location.href = './exhibitors3.html?by=search' + (request['from'] ? '&from=' + request['from'] : '') + (request['info'] ? '&info=' + request['info'] : '') + '&keyword=' + this.value;
        location.href = './exhibitors1.html?by=search' + (request['from'] ? '&from=' + request['from'] : '') + (request['info'] ? '&info=' + request['info'] : '') + '&keyword=' + this.value;
    }
}, false);

$("#iptSearch").attr('placeholder',lng.searchtips);


if(from=="schedule") {
    $('#backurl').attr('href','../schedule/mine.html');
}
else {
    $('#backurl').attr('href','../dashboard/index.html');
}